<template>
  <div :class="$root.themeHomeChange === '1' ? 'project-manage-page overViewManage_light' : 'project-manage-page'">
    <div class="form-container">
      <manage-form
        @addTableData="addTableData"
        @initTable="initTable"
        ref="projectManageForm">
      </manage-form>
    </div>
    <div class="table-container">
      <manage-table
        ref="projectManageTable"
        :params="params"
        @editTask="editTask"
        @exportTable="exportTable">
      </manage-table>
    </div>
  </div>
</template>
<script>
  import manageForm from "../component/manageForm";
  import manageTable from "../component/manageTable";
  import Interface from "@/base/mixin/risk-control-center.js";
  
  export default {
    name: 'projectManagePage',
    components: {manageTable, manageForm},
    mixins: [Interface],
    data(){
      return {
        params: {}
      }
    },
    watch: {
    },
    created() {
    },
    mounted() {
      this.$refs.projectManageTable.initTable({}, true);
    },
    methods: {

      /**
       * @description:更新表格
       */
      reloadTable(){

      },

      /**
       * @description:导出表格
       */
      exportTable(){
      },

      addTableData(data){
        this.$refs.projectManageTable.tableData.push(data)
      },
      
      editTask(data) {
        this.$refs.projectManageForm.editDialogClick(data);
      },
      
      initTable(params) {
        window.console.log(params);
        this.params = params;
        this.$refs.projectManageTable.initTable(params, true);
      }
    },
  }
</script>

<style lang="scss" scoped>
  .project-manage-page {
    background: none;
    .table-container{
      margin-top: 10px !important;
    }
  }
  
  // 浅色主题
  .overViewManage_light {
    .form-container {
      background-color: rgba($color: #000000, $alpha: 0)!important;
    }
    .table-container {
      background-color: rgba($color: #000000, $alpha: 0)!important;
    }
  }
  
</style>